<template>
  <div>
    <!-- 主要内容区 -->
    <div class="main">
      <div class="list">
        <MyList :list="product" v-if="product.length>0"></MyList>
        <div v-else class="none-product">抱歉没有找到相关的商品，请看看其他的商品</div>
      </div>
      <!-- 分页 -->
      <div class="pagination">
        <el-pagination
          background
          layout="prev, pager, next"
          :page-size="pageSize"
          :total="total"
          @current-change="currentChange"
        ></el-pagination>
      </div>
      <!-- 分页END -->
    </div>
    <!-- 主要内容区END -->
  </div>
</template>

<script>
export default {
  data(){
    return{
      product:"",
    }

  },
    created(){
      this.getPromo()

    },
    methods:{
       getPromo( ) {
       this.$axios(   
            {
                url: "http://localhost:3000/cart", 
            //   url: this.$target+"/goodsByCategoryId", 
              method: 'get',
              params:{
               currentPage: 1,
              pageSize: 5,
              }
        })
        .then(res => {
           this.product = res.data.data;
         })
        .catch(err => {
          return Promise.reject(err);
        });     
    }
  }
}
</script>

